'use strict';

/**
* Test if a value is a skew-symmetric matrix.
*
* @module @stdlib/assert/is-skew-symmetric-matrix
*
* @example
* var ctor = require( '@stdlib/ndarray/ctor' );
* var isSkewSymmetricMatrix = require( '@stdlib/assert/is-skew-symmetric-matrix' );
*
* var ndarray = ctor( 'generic', 2 );
*
* var arr = ndarray( [ 0, -1, 1, 0 ], [ 2, 2 ], [ 2, 1 ], 0, 'row-major' );
*
* var bool = isSkewSymmetricMatrix( arr );
* // returns true
*
* bool = isSkewSymmetricMatrix( [] );
* // returns false
*/

// MODULES //
var isSkewSymmetricMatrix = __var_0__;


// EXPORTS //
export default isSkewSymmetricMatrix;
